import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { ofType, createEffect, Actions } from "@ngrx/effects";
import { catchError, map, switchMap, take } from "rxjs/operators";

import { LocalStorageService } from "../local-storage/local-storage.service";

import {socialAuthLogin, socialAuthLoginFail, socialAuthLoginSuccess} from "./auth.actions";
import {GoogleLoginProvider, SocialAuthService, SocialUser} from 'angularx-social-login';
import {from, of} from 'rxjs';
import {HttpClient, HttpHeaders} from '@angular/common/http';

export const AUTH_KEY = "AUTH";

@Injectable()
export class AuthEffects {

  loginSocial = createEffect(
      () => this.actions$.pipe(
          ofType(socialAuthLogin),
          take(1),
          switchMap( (action) =>
             from(this.socialAuthService.signIn(GoogleLoginProvider.PROVIDER_ID, {state: '12345'})).pipe(
                 switchMap( (user : SocialUser) => {
                     console.log(user);
                     this.http.post('http://localhost:5000/who', user, {headers: new HttpHeaders({"Content-Type":"application/json"})}).toPromise().then((res)=>window.alert(JSON.stringify(res)));
                     return this.socialAuthService.authState.pipe(map(user =>socialAuthLoginSuccess({user})))
                 }),
                 catchError((err) => {
                     console.log(err)
                     return of(socialAuthLoginFail({err}))
                 } ),
             )
          )
      )
  );

  constructor(
    private actions$: Actions,
    private localStorageService: LocalStorageService,
    private socialAuthService: SocialAuthService,
    private http : HttpClient,
    private router: Router
  ) {}
}
