import {createAction, props} from "@ngrx/store";
import {SocialUser} from 'angularx-social-login';


export const socialAuthLogin = createAction("[Auth] Social Login");
export const socialAuthLoginUnauthorized = createAction("[Auth] Social Login Unauthorized");
export const socialAuthLoginSuccess = createAction(
    "[Auth] Social Login Success",
        props<{user : SocialUser}>(),
);
export const socialAuthLoginFail = createAction(
    "[Auth] Social Login Fail",
    props<{err: any}>()
);
export const authLogout = createAction("[Auth] Logout");
