import browser from "browser-detect";
import { Component, OnInit } from "@angular/core";
import { Store, select } from "@ngrx/store";
import { Observable } from "rxjs";

import { environment as env } from "../../environments/environment";

import {
  authLogout,
  routeAnimations,
  LocalStorageService,
  selectAuthUser,
  selectSettingsStickyHeader,
  selectSettingsLanguage,
  selectEffectiveTheme,
} from "../core/core.module";
import {
  actionSettingsChangeAnimationsPageDisabled,
  actionSettingsChangeLanguage,
} from "../core/settings/settings.actions";
import {socialAuthLogin} from '../core/auth/auth.actions';
import {SocialUser} from 'angularx-social-login';

@Component({
  selector: "jurador-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
  animations: [routeAnimations],
})
export class AppComponent implements OnInit {
  isProd = env.production;
  envName = env.envName;
  version = env.versions.app;
  year = new Date().getFullYear();
  logo = require("../../assets/logo.png").default;
  languages = ["en", "pt-br"];
  navigation = [
    { link: "about", label: "jurador.menu.about" },
    { link: "feature-list", label: "jurador.menu.features" },
    { link: "examples", label: "jurador.menu.examples" },
  ];
  navigationSideMenu = [
    ...this.navigation,
    { link: "settings", label: "jurador.menu.settings" },
  ];

  authenticatedUser$: Observable<SocialUser>;
  stickyHeader$: Observable<boolean>;
  language$: Observable<string>;
  theme$: Observable<string>;

  constructor(
    private store: Store,
    private storageService: LocalStorageService
  ) {}

  private static isIEorEdgeOrSafari() {
    return ["ie", "edge", "safari"].includes(browser().name);
  }

  ngOnInit(): void {
    this.storageService.testLocalStorage();
    if (AppComponent.isIEorEdgeOrSafari()) {
      this.store.dispatch(
        actionSettingsChangeAnimationsPageDisabled({
          pageAnimationsDisabled: true,
        })
      );
    }

    this.authenticatedUser$ = this.store.pipe(select(selectAuthUser));
    this.stickyHeader$ = this.store.pipe(select(selectSettingsStickyHeader));
    this.language$ = this.store.pipe(select(selectSettingsLanguage));
    this.theme$ = this.store.pipe(select(selectEffectiveTheme));
  }

  onLoginClick() {
    this.store.dispatch(socialAuthLogin());
  }

  onLogoutClick() {
    this.store.dispatch(authLogout());
  }

  onLanguageSelect({ value: language }) {
    this.store.dispatch(actionSettingsChangeLanguage({ language }));
  }
}
